<template>
  <div class="cms-list-layout">
    <cms-prompt  collapse='true'>
      <li>显示所有会员等级的信息。</li>
      <li>可以添加会员等级、编辑会员等级、删除会员等级。</li>
    </cms-prompt>
    <section class="cms-list-query">
      <div  class="query-group">
        <label>会员等级</label>
        <el-input v-model="queryParams.levelName" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <section class="cms-batch">
        <cms-button icon="add" type="success" size="small" @click="$routerLink('/memberGrade/save','save')">新增</cms-button>
      <cms-button icon="error" type="error" size="small" @click="deleteDatas(ids)">批量删除</cms-button>
    </section>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop="levelName"  label="会员等级" width="110"></el-table-column>
      <el-table-column prop="levelExp" label="升级条件（成长值）" min-width="220" align="center"></el-table-column>
      <el-table-column prop="levelRights" label="权益" min-width="220" align="center">
        <div slot-scope="scope">
          <span v-for="(item,index) in scope.row.levelRights" :key="index" >{{item.type | codeFiller}}</span>
        </div>
      </el-table-column>
      <el-table-column label="操作选项" width='250' fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button icon='edit' size='small' type='info' @click="$routerLink('/memberGrade/rankMember','list',{id:scope.row.id})">等级会员</cms-button>
          <cms-button icon='edit' size='small' type='info' @click="$routerLink('/memberGrade/update','update',{id:scope.row.id})">修改</cms-button>
          <cms-button icon='error' size='small' type='info' @click="deleteData(scope.row.id)">删除</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>

<script>
import { memberlevelList, memberlevelDelete } from './../../../../api/users/level.js'
import list from '@/mixins/list'
var defaultData = {
  roleName: '',
  all: '',
  page: 1,
  size: 15
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    toGroup(row, column) { // 可过滤传递内容

    },
    deleteData(id) { // 包装一层删除的操作
      this.deleteTableData(memberlevelDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas(ids) {
      this.deleteTableDatas(memberlevelDelete, ids) // 这个才是真实的删除方法
    }
  },
  filters: {
    codeFiller(val) {
      switch (val) {
        case '1':
          return '购物福利 '
        case '2':
          return '生日特权 '
        default:
      }
    }
  },
  created() {
    this.getTableData(memberlevelList, this.queryParams)
  }
}
</script>

<style>
</style>



